html {
	box-sizing: border-box;
	font-size: 16px;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html,
body {
	height: 100%;
	position: relative;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

button:focus,
input:focus {
	outline: transparent;
}

h5 {
	font-size: 20px;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

body{
    font-family: 'Source Sans Pro', sans-serif;
    background-color: #fff;
    color: $gray;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    position: relative;
    
}
.wrapper{
    max-width: 1920px;
    margin:0 auto;
    position: relative;
}
.wrapper-content{
    max-width: 1235px;
    margin: 0 auto ;
    
}